"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { categories } from "@/lib/dummy-data"
import { useStore } from "@/contexts/store-context"
import { ArrowRight, ShoppingBag, Star } from "lucide-react"
import { HeroCarousel } from "@/components/hero-carousel"
import { Badge } from "@/components/ui/badge"

export default function HomePage() {
  const { products } = useStore()
  const featuredProducts = products.filter((p) => p.stock > 0).slice(0, 4) // Show 4 products for better grid

  return (
    <div className="min-h-screen">

      {/* Hero Section with Carousel */}
      <section className="pt-8 pb-12 px-4 md:px-6">
        <HeroCarousel />
      </section>

      <div className="container mx-auto px-4 pb-20 space-y-20">

        {/* Categories Section - Classy Minimalist */}
        <section>
          <div className="flex items-center justify-between mb-8">
            <div>
              <h2 className="text-3xl font-bold tracking-tight text-primary">Curated Collections</h2>
              <p className="text-muted-foreground mt-1">Explore our range of premium branded merchandise.</p>
            </div>
            <Link href="/categories" className="hidden md:flex items-center text-primary font-medium hover:underline">
              View All Categories <ArrowRight className="ml-2 w-4 h-4" />
            </Link>
          </div>

          <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
            {categories.map((category) => (
              <Link key={category} href={`/categories?category=${category}`} className="group">
                <Card className="h-full border-none shadow-sm hover:shadow-xl transition-all duration-300 bg-white/50 backdrop-blur-sm group-hover:-translate-y-1 overflow-hidden">
                  <div className="p-8 flex flex-col items-center justify-center text-center h-40 relative">
                    <div className="absolute inset-0 bg-gradient-to-br from-primary/5 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />
                    <ShoppingBag className="w-10 h-10 mb-4 text-primary/80 group-hover:scale-110 transition-transform duration-300" />
                    <h3 className="font-semibold text-xl group-hover:text-primary transition-colors">{category}</h3>
                  </div>
                </Card>
              </Link>
            ))}
          </div>
        </section>

        {/* Featured Products - Professional Grid */}
        <section>
          <div className="flex items-center justify-between mb-8">
            <div>
              <h2 className="text-3xl font-bold tracking-tight text-primary">Featured Arrivals</h2>
              <p className="text-muted-foreground mt-1">Handpicked quality items, ready for you.</p>
            </div>
            <Link href="/categories" className="hidden md:flex items-center text-primary font-medium hover:underline">
              Shop All Products <ArrowRight className="ml-2 w-4 h-4" />
            </Link>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
            {featuredProducts.map((product) => (
              <Link key={product.id} href={`/products/${product.id}`} className="group h-full">
                <Card className="h-full overflow-hidden border-none shadow-sm hover:shadow-2xl transition-all duration-300 bg-white flex flex-col group-hover:-translate-y-1">
                  <div className="relative aspect-[4/3] overflow-hidden bg-gray-100">
                    <img
                      src={product.image || "/placeholder.svg"}
                      alt={product.name}
                      className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                    />
                    <div className="absolute top-3 right-3">
                      <Badge className="bg-white/90 text-primary hover:bg-white shadow-sm backdrop-blur-md">
                        New
                      </Badge>
                    </div>
                    {/* Overlay on hover */}
                    <div className="absolute inset-x-0 bottom-0 p-4 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex justify-center pb-6 bg-gradient-to-t from-black/50 to-transparent">
                      <Button size="sm" className="w-full bg-white text-primary hover:bg-gray-100 font-medium">
                        Quick View
                      </Button>
                    </div>
                  </div>

                  <CardContent className="p-5 flex-grow">
                    <div className="flex items-center justify-between mb-2">
                      <p className="text-xs font-semibold tracking-wider text-muted-foreground uppercase">{product.category}</p>
                      <div className="flex items-center text-yellow-500 text-xs">
                        <Star className="w-3 h-3 fill-current" />
                        <span className="ml-1 text-muted-foreground">4.8</span>
                      </div>
                    </div>
                    <h3 className="font-bold text-lg mb-2 text-foreground line-clamp-1 group-hover:text-primary transition-colors">
                      {product.name}
                    </h3>
                    <p className="text-sm text-muted-foreground line-clamp-2 mb-4">
                      {product.description}
                    </p>
                  </CardContent>

                  <CardFooter className="p-5 pt-0 border-t border-gray-100/50 mt-auto flex items-center justify-between">
                    <span className="text-xl font-bold text-primary">â‚¹{product.price}</span>
                    <Button variant="ghost" size="icon" className="rounded-full hover:bg-primary/10 hover:text-primary">
                      <ShoppingBag className="w-5 h-5" />
                    </Button>
                  </CardFooter>
                </Card>
              </Link>
            ))}
          </div>
        </section>

        {/* Trust/Business Features Banner */}
        <section className="rounded-2xl bg-primary text-primary-foreground p-12 overflow-hidden relative">
          <div className="absolute inset-0 bg-[url('/grid-pattern.svg')] opacity-5 mix-blend-overlay"></div>
          <div className="relative z-10 grid md:grid-cols-3 gap-8 text-center md:text-left">
            <div className="space-y-4">
              <div className="w-12 h-12 bg-white/10 rounded-full flex items-center justify-center mx-auto md:mx-0">
                <ShieldCheckIcon className="w-6 h-6" />
              </div>
              <h3 className="text-xl font-bold">Premium Quality</h3>
              <p className="text-primary-foreground/80">Certified materials and craftsmanship for lasting professional impression.</p>
            </div>
            <div className="space-y-4">
              <div className="w-12 h-12 bg-white/10 rounded-full flex items-center justify-center mx-auto md:mx-0">
                <TruckIcon className="w-6 h-6" />
              </div>
              <h3 className="text-xl font-bold">Fast Delivery</h3>
              <p className="text-primary-foreground/80">Priority shipping options available for bulk corporate orders.</p>
            </div>
            <div className="space-y-4">
              <div className="w-12 h-12 bg-white/10 rounded-full flex items-center justify-center mx-auto md:mx-0">
                <HeadphonesIcon className="w-6 h-6" />
              </div>
              <h3 className="text-xl font-bold">Dedicated Support</h3>
              <p className="text-primary-foreground/80">24/7 customer service to assist with your branding needs.</p>
            </div>
          </div>
        </section>
      </div>
    </div>
  )
}

function ShieldCheckIcon(props: any) {
  return (
    <svg
      {...props}
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <path d="M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10" />
      <path d="m9 12 2 2 4-4" />
    </svg>
  )
}

function TruckIcon(props: any) {
  return (
    <svg
      {...props}
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <path d="M14 18V6a2 2 0 0 0-2-2H4a2 2 0 0 0-2 2v11a1 1 0 0 0 1 1h2" />
      <path d="M15 18H9" />
      <path d="M19 18h2a1 1 0 0 0 1-1v-3.65a1 1 0 0 0-.22-.624l-3.48-4.35A1 1 0 0 0 17.52 8H14" />
      <circle cx="17" cy="18" r="2" />
      <circle cx="7" cy="18" r="2" />
    </svg>
  )
}

function HeadphonesIcon(props: any) {
  return (
    <svg
      {...props}
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <path d="M3 14v3a2 2 0 0 0 2 2h2a2 2 0 0 0 2-2v-3a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2Z" />
      <path d="M15 14v3a2 2 0 0 0 2 2h2a2 2 0 0 0 2-2v-3a2 2 0 0 0-2-2h-2a2 2 0 0 0-2 2Z" />
      <path d="M4 14v-3a8 8 0 1 1 16 0v3" />
    </svg>
  )
}
